<template>
  <v-chart class="chart chart-right" :option="option" />
</template>
<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { BarChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";

use([
  CanvasRenderer,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

const labelOption = {
      rotate: 90,
      align: 'left',
      verticalAlign: 'middle',
      position: 'insideBottom',
      distance: 15
    };

export default ({
  name: "BarTest",
  components: {
    VChart,
  },
  provide: {
    [THEME_KEY]: "dark"
  },
  data(){
    return{
        abnrmlValue:0.7,
        normalCount:[0,0,0],
        abnrmlCount:[0,0,0],
        option:{
            title: {
                text: 'Type of Signal',
                subtext: 'Sample Generated Data',
                left: 'left'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                type: 'shadow'
                }
            },
            legend: {
                data: ['정상신호', '이상신호']
            },
            xAxis: [
                {
                type: 'category',
                axisTick: { show: false },
                data: ['센서1', '센서2', '센서3']
                }
            ],
            yAxis: [
                {
                type: 'value'
                }
            ],
            series: [
                {
                    name: '정상신호',
                    type: 'bar',
                    barWidth: '20%',
                    barGap: '30%',
                    label: labelOption,
                    emphasis: {
                        focus: 'series'
                    },
                    data: [0, 0, 0]
                },
                {
                    name: '이상신호',
                    type: 'bar',
                    barWidth: '20%',
                    barGap: '30%',
                    label: labelOption,
                    emphasis: {
                        focus: 'series'
                    },
                    data: [0, 0, 0]
                }
            ]
        }
    }
  },
  methods: {
    getData(list){
      let self = this;
      self.parser(list)
    },
    parser(res){
      let self = this;

      if(res[res.length-1].val1>this.abnrmlValue) self.abnrmlCount[0]++;
      else self.normalCount[0]++;

      if(res[res.length-1].val2>this.abnrmlValue) self.abnrmlCount[1]++;
      else self.normalCount[1]++;

      if(res[res.length-1].val3>this.abnrmlValue) self.abnrmlCount[2]++;
      else self.normalCount[2]++;
      self.option.series[0].data=[];
      self.option.series[1].data=[];
      self.option.series[0].data=self.normalCount;
      self.option.series[1].data=self.abnrmlCount;
    }
  },
})
  
</script>