<template>
  <v-chart class="chart chart-left" :option="option" />
</template>
<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";

use([
  CanvasRenderer,
  PieChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

export default ({
  name:"PieTest",
  components: {
    VChart,
  },
  provide: {
    [THEME_KEY]: "dark"
  },
  data(){
    return{
      count:[
          {value: 0, name: '센서1'},
          {value: 0, name: '센서2'},
          {value: 0, name: '센서3'}],
      abnrmlValue:0.7,
      option:{
        title: {
            text: 'Abnormal Signal Rate of Sensor',
            subtext: 'Sample Generated Data',
            left: 'left'
        },
        tooltip: {
            trigger: 'item'
        },
        legend: {
            top: 'top'
        },
        series: [
            {
            name: '이상신호 발생 비율',
            type: 'pie',
            radius: '60%',
            data: [
                { value: 0, name: '센서1' },
                { value: 0, name: '센서2' },
                { value: 0, name: '센서3' },
            ],
            emphasis: {
                itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
            }
        ]
      }
    }
  },
  methods: {
    getData(list){
      let self = this;
      self.parser(list)
    },
    parser(res){
      let self = this;
      if(res[res.length-1].val1>this.abnrmlValue) self.count[0].value++;
      if(res[res.length-1].val2>this.abnrmlValue) self.count[1].value++;
      if(res[res.length-1].val3>this.abnrmlValue) self.count[2].value++;
      self.option.series[0].data=self.count;
    }
  },
})
  
</script>
