<template>
  <v-chart class="chart" :option="option" />
</template>
<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";

use([
  CanvasRenderer,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

export default ({
  name:"LineTest",
  components: {
    VChart,
  },
  provide: {
    [THEME_KEY]: "dark"
  },
  data(){
    return{
      dataList:[],
      labelList:[],
      option:{
        title: {
          text: 'Demo Monitoring Data & Time Axis'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            animation: false
          }
        },
        legend: {
          data: ['센서1','센서2','센서3']
        },
        xAxis: {
          type: 'category',
          data: [],
          boundaryGap: false,
          // splitLine: {
          //   show: true
          // }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            show: false
          },
          min:0,
          max:1.5
        },
        series: [
          {
            name: '센서1',
            type: 'line',
            // showSymbol: false,
            // data: this.dataList
            data: []
          },
          {
            name: '센서2',
            type: 'line',
            data: []
          },
          {
            name: '센서3',
            type: 'line',
            data: []
          }
        ]
      }
    }
  },
  methods: {
    getData(list){
      let self = this;
      self.parser(list)
    },
    parser(res){
      let self = this;
      if(self.option.series[0].data.length>50) {
        self.option.series[0].data.shift();
        self.option.series[1].data.shift();
        self.option.series[2].data.shift();
        self.option.xAxis.data.shift();
      }
      self.option.xAxis.data.push(this.$moment(res[res.length-1].regDt).format('YYYY-MM-DD HH:mm:ss'));

      self.option.series[0].data.push(res[res.length-1].val1);
      self.option.series[1].data.push(res[res.length-1].val2);
      self.option.series[2].data.push(res[res.length-1].val3);
      
    }
  },
})
  
</script>
