import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import vueMoment from "vue-moment";
import vueChart from "vue-chartjs";


Vue.config.productionTip = false;

Vue.use(vueMoment);
Vue.use(vueChart);
var vm = new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
