<template>
<div>
  <button @click="startLoop" style="position: absolute; z-index:9999999; top:5%; right:62%;">수집시작</button>
  <button @click="stopLoop" style="position: absolute; z-index:9999999; top:5%; right:56%;">수집중단</button>
  <button @click="getData" style="position: absolute; z-index:9999999; top:5%; right:50%;">데이터수집</button>
  <p @click="getData" style="position: absolute; z-index:9999999; right:5%; color:white; top: 2em;">Time of Occurrence : {{time | moment('YYYY-MM-DD HH:mm:ss')}}</p>
  <LineTest ref="LineTest"></LineTest>
  <PieTest ref="PieTest"></PieTest>
  <BarTest ref="BarTest"></BarTest>
</div>
</template>

<script>
import axios from 'axios';
import LineTest from '@/component/LineChart.vue'
import PieTest from '@/component/PieChart.vue'
import BarTest from '@/component/BarChart.vue'

export default ({
  name: "HelloWorld",
  components: {
    "LineTest":LineTest,
    "PieTest":PieTest,
    "BarTest":BarTest,
  },
  data(){
    return{
      time:'',
      interval:'',
    }
  },
  methods: {
    getData(){
      let self = this;
      axios.get('https://etei.ksec.synology.me/api/stn2/realTime').then(function(response) {
        self.$refs.LineTest.getData(response.data)
        self.$refs.PieTest.getData(response.data)
        self.$refs.BarTest.getData(response.data)
        self.time = response.data[response.data.length-1].regDt;
      });
    },
    startLoop(){
      let self = this;
      self.interval = setInterval(() => self.getData(), 1000);
    },
    stopLoop(){
      let self = this;
      clearInterval(self.interval);
    },
  },
})
  
</script>

<style scoped>
.chart {
  height: 50vh;
}
.chart-left, .chart-right{
  width:50vw;
}
.chart-left{
  float: right;
}
</style>

<style>
body {
  margin: 0;
}
</style>